html,
body {
  margin: 0;
  height: 100%;
  width: 100%;
}
canvas {
  height: 100%;
  width: 100%;
  display: block;
}

.effect-container {
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}
.effect {
  padding: 100px;
}
.effect__img {
  opacity: 0.2;
}

.section__copy {
  overflow:hidden;
  font-size: 60px;
  padding-top: 812px;
  transition-duration: 0.5s;
  padding-left: 90px;
  padding-right: 330px;
}

.section__copy--hide {
  display: none;
  opacity: 0;
}
.section__copy--show {
  opacity: 1;
}

.header--close {
  position: fixed;
  top: 20px;
  right: 120px;
  cursor: pointer;
}
